.menu {
  overflow: auto;
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  @include on-desktop {
    font-weight: 700;
  }

  &__top {
    margin-bottom: 24px;
    position: sticky;

    @include on-tablet {
      margin-bottom: 8px;
    }

    @include on-desktop {
      margin-bottom: 24px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    width: fit-content;
    font-size: 16px;
    line-height: 27px;
    color: black;
    text-decoration: none;
  }

  &__book-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: black;
    text-decoration: none;
  }
}
