.header {
  height: 100vh;
  background-image: url(/src/images/bg-img/header-bg-1.avif);
  background-size: cover;
  background-position: 80%;
  background-repeat: no-repeat;
  user-select: none;

  @include on-tablet {
    background-position: center;
  }

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;

    @include on-tablet {
      font-size: 64px;
    }

    @include on-desktop {
      font-size: 80px;
    }
  }

  &__bottom {
    padding-bottom: 88px;

    @include on-tablet {
      padding-bottom: 130px;
    }

    @include on-desktop {
      padding-bottom: 203px;
    }
  }
}
